<template>
  <div class="footer">
    <div v-if="address" class="address button1 address-copy" :data-clipboard-text="address">
      {{address.substring(0,12)}}...{{address.substring(address.length - 15, address.length)}} <img class="copy" src="@/assets/images/copy.webp" alt="">
    </div>
    <div v-else class="address button1" @click="initWallet">
      {{t('index.鏈接錢包')}}
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, computed, watch, onMounted } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import web3Data from "@/utils/initWeb3";
import Clipboard from "clipboard";
import { Toast } from "vant";

export default {
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      address: computed(() => store.state.accounts[0]),
      t: t,
      initWallet: () => {
        web3Data.initWeb3()
      },
      initData: () => {
      }
    })
    if (data.address) {
      data.initData();
    }
    onMounted(() => {
      var clipboard = new Clipboard(".address-copy");
      clipboard.on("success", function (e) {
        Toast(t("share.複製成功"));
        e.clearSelection();
      });
      clipboard.on("error", function () {
        Toast(t("share.複製失敗，請手動長按複製"));
      });
    });
    watch(() => data.address, (val) => {
      if (val) {
        data.initData()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.footer {
  max-width: 640px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0 16px 18px;
  margin: 0 auto;
  box-sizing: border-box;
}

.address {
  max-width: 640px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 13px;
  color: #fff;
  height: 40px;
  line-height: 40px;
  .copy {
    width: 14px;
    height: 14px;
    display: block;
    margin-left: 6px;
  }
}
</style>
